import { writable } from 'svelte/store';
import axios from 'axios';
import { axiosError } from './common.js';

class MembersStore {
  members = writable([]);
  member = writable({
    name: '',
    age: ''
  });

  membersCreate(member) {
    axios.post('https://ovdncids-test1.herokuapp.com/api/v1/members', member).then((response) => {
      console.log('Done membersCreate', response);
      this.membersRead();
    }).catch((error) => {
      axiosError(error);
    });
  };

  membersRead() {
    axios.get('https://ovdncids-test1.herokuapp.com/api/v1/members').then((response) => {
      console.log('Done membersRead', response);
      this.members.set(response.data.members);
    }).catch((error) => {
      axiosError(error);
    });
  };

  membersDelete(index) {
    axios.delete('https://ovdncids-test1.herokuapp.com/api/v1/members/' + index).then((response) => {
      console.log('Done membersDelete', response);
      this.membersRead();
    }).catch((error) => {
      axiosError(error);
    });
  };

  membersUpdate(index, member) {
    axios.patch('https://ovdncids-test1.herokuapp.com/api/v1/members/' + index, member).then((response) => {
      console.log('Done membersUpdate', response);
      this.membersRead();
    }).catch((error) => {
      axiosError(error);
    });
  };
}

export default new MembersStore();
